import store from '../../assets/share/driverShare'
import Tou from '../Top/top'
  export default {
    name: "chat",
    components:{
      Tou
    },
    data:function() {
      return {
        message:'',
        random:'',
        oid:'',
        messageArr:[],
        currentTime:''
      }
    },
    created:function() {
        this.currentTime = new Date().toLocaleString()
      if(this.$route.params.oid) {
        console.log("chat_route................." + this.$route.params.oid)
        this.oid = this.$route.params.oid
        this.messageArr = this.$route.params.messageArr

      }
    },
    sockets:{
      connect() {
        this.$socket.emit("groupId", this.oid)
        this.$socket.emit(this.oid + "chatGroup")
      },
      userMessage(value) {
        this.messageArr.push({"message":value,className:"userMes"})
      }
    },
    methods:{
      send() {
       console.log("send===================================================")
        this.$socket.emit("driverMessage", this.message)
        this.messageArr.push({"message":this.message,className:"driverMes"})
      }
    },
    store
  }



