import '../assets/js/base.js'
import store from '../assets/share/driverShare'
import Prompt from './tips/tankuang'
  export default {
      name: "index",
    components:{
      Prompt
    },
    data:function(){
        return {
          orders:[],//订单存储在数组中
          order: {
            username:'',
            getOn:'',
            getOff:'',
            onTime:'',
            pNum:0,
            luggages:0,
            phoneNum:'',
            oid:'',
            isShow:false
          },
          isLogin:false,
          isOrderComing:false,
          msg:'',
          flag:false,
          error:'',
          userData:{
            cancel_username:'',
            cancel_getOn:'',
            cancel_onTime:'',
            cancel_getOff:''
          },
          cancelShow:false,
          menuShow:false
        }
    },
    created:function() {
      this.orders = this.$store.state.orders;
      console.log(this.orders.length)
      console.log(this.orders)
      this.judgeShow()
      let This = this;
      this.$http.get(this.$store.state.baseUrl+"/getDriver").then(function(res) {
        if(Object.prototype.toString.call(res.data) === "[object Object]") {
          This.$store.state.driver = res.data;
          /*console.log("init.............")*/
          this.isLogin = true;
          console.log(this.isLogin);
        }else {
          this.isLogin = false;
        }
      })
    },
    sockets:{
      connect(){
        console.log("vue已连接成功！！")
      },
      orderData(value) {
        console.log("orderdata........")
        console.log(value)
        if (this.isLogin) {
          this.getOrder(value.order.oid, value.user)
        }
      },
      orderCancel(value) {
          this.userData.cancel_username = value.userData.username;
          this.userData.cancel_getOn = value.userData.getOn;
          this.userData.cancel_getOff = value.userData.getOff;
          this.userData.cancel_onTime = value.userData.onTime;
          this.cancelShow = true;
      },
      takeError(value) {
        console.log(value)
      },
      orderCancelToAll(value) {
        console.log(value)
        for(var i = 0; i < this.orders.length; i++) {
          if(this.orders[i].oid == value.oid) {
            this.orders.splice(i, 1);
            break;
          }
        }
        this.judgeShow()
      }
    },
    methods:{
      takeOrder(index){
          var This = this
          //this.isOrderComing = false;
          this.$socket.emit("groupId", this.orders[index].oid)
          this.$socket.emit(this.orders[index].oid  + "chatGroup")
          this.$socket.emit("takenSuccess", this.$store.state.driver)
          this.orders[index] = JSON.stringify(this.orders[index])
          this.$http.post(this.$store.state.baseUrl+"/takeOrder", {order:this.orders[index]}, {emulateJSON:true}).then(function(res) {
            if(Object.prototype.toString.call(res.data) === "[object Object]") {
              this.orders.splice(index, 1)
              This.$router.push({
                path:"/ordering",
                query:{
                  oid:res.data.oid
                }
              })
            }else {
              this.flag = true
              this.error = res.data
              let timer = setTimeout(()=>{
                This.error = ''
                clearTimeout(timer);
              },1000)
            }
          })
        },
      disappear(index) {
        this.orders.splice(index, 1)
        console.log(this.orders)
      },
      cancelDisappear() {
        this.cancelShow = false;
      },
      getOrder(oid, user) {
          this.$http.post(this.$store.state.baseUrl + "/getUserOrder", {oid:oid,uid:user.uid},{emulateJSON:true}).then(function(res){
            if(res.data) {
              console.log(res.data)
              this.order = res.data;
              this.order.uid =user.uid
              this.order.did = this.$store.state.driver.did
              this.order.username = user.username
              this.order.phoneNum = user.phoneNum
              console.log("length............." + this.orders.length)
              if(!this.contains(this.order)){
                this.orders.push(this.order)
                this.$store.state.orders = this.orders
                this.judgeShow()
              }
              /*this.isOrderComing = true;*/
            }
          })
      },
      toLogin() {
          this.$router.push("/login")
      },
      toRegist() {
          this.$router.push("/regist")
      },
      signOut(){
        this.menuShow = false;
        this.$http.get(this.$store.state.baseUrl+'/driver/signOut').then(res=>{
          if(res.data){
            console.log("true")
            this.isLogin = false;
          }else {
            console.log("false")
            this.isLogin = true;
          }
        })
      },
      judgeShow() {
        if(this.orders.length) {
          this.isOrderComing = true;
        }else {
          this.isOrderComing = false;
        }
      },
      contains(order) {
        let flag = false;
        for(var i = 0; i < this.orders.length; i++) {
          if(this.orders[i].oid == order.oid) {
            flag = true;
          }
        }
        return flag;
      }
    },
    store
  }



