import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index'
import regist from '@/components/regist/regist'
import login from '@/components/login/login'
import registSuccess from '@/components/msg/registSuccess'
import ordering from '@/components/driver/ordering'
import orderOver from '@/components/driver/orderOver'
import overMsg from '@/components/driver/overMsg'
import chat from '@/components/driver/chat'
Vue.use(Router);

export default new Router({
/*  mode:'history',*/
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path: '/regist',
      name: 'regist',
      component: regist
    },
    {
      path: '/login',
      name: 'login',
      component: login
    },
    {
      path: '/registSuccess',
      name: 'registSuccess',
      component: registSuccess
    },
    {
      path: '/ordering',
      name: 'ordering',
      component: ordering
    }
    ,
    {
      path:'/orderOver',
      name:'orderOver',
      component:orderOver
    },
    {
      path:'/overMsg',
      name:'overMsg',
      component:overMsg
    },
    {
      path:'/chat',
      name:'chat',
      component:chat
    }

  ]
})



