import store from '../../assets/share/driverShare'
import Tou from '../Top/top'
  export default {
      name: "order-o-ver",
    components: {
      Tou
    },
      data:function() {
        return{
          order:{
            oid:'',
            username:'',
            getOn:'',
            getOff:'',
            onTime:'',
            pNum:'',
            luggages:'',
            phoneNum:'',
            realCost:'',
            uid:''
          }
        }
      },
    created:function(){
        console.log("infewewfwefwef")
      /*if(this.$store.state.driver.eitherCarried) {
          console.log("refresh/..............")
          this.$router.go(0)
      }*/
      /*if(this.eitherFresh) {
        console.log("eitherFresh")
        this.eitherFresh = false;
        this.sendFinish()
      }*/
      if(this.$route.query.oid) {
        console.log("oid=============")
        console.log(this.$route.query.oid)
        this.order.oid = this.$route.query.oid
      }
      let This = this;
      this.$http.get(this.$store.state.baseUrl + "/getOrder", {params:{oid:this.order.oid}}).then(function(res) {
        if (res.data) {
          console.log(res.data)
          res.data.realCost = res.data.realCost.toFixed(2)
          This.order.username = res.data.username
          This.order.getOn = res.data.getOn
          This.order.getOff = res.data.getOff
          This.order.onTime = res.data.onTime
          This.order.pNum = res.data.pNum
          This.order.luggages = res.data.luggages
          This.order.phoneNum = res.data.phoneNum
          This.order.oid = res.data.oid
          This.order.realCost = res.data.realCost
          This.order.uid = res.data.uid
          This.order = res.data;
        }
      })
    },
    methods:{
        sendFinish() {
          this.$http.get(this.$store.state.baseUrl + "/finish", {params:{oid:this.order.oid}}).then(function(res) {
            if(res.data) {
              this.$socket.emit("over", this.order.uid)
              /*this.$store.state.driver.eitherOverFresh = true;*/
              this.$router.push(
                {
                  name:"overMsg",
                  params:{
                      oid:this.order.oid
                  }
                }
              )
            }
          })
        }
    },
    store
   }



