import store from '../../assets/share/driverShare'
import Tou from '../Top/top'
import OrderModel from '../orderModel/orderModel'
  export default {
      name: "over-msg",
    components: {
      Tou,
      OrderModel
    },
    created() {
      if(this.$route.params.oid)  {
        this.order.oid = this.$route.params.oid
      }

    },
    sockets:{
      connect() {
        this.$socket.emit("groupId", this.oid)
        this.$socket.emit(this.oid + "chatGroup")
      },

      commentMsg(value){
        console.log("value_commentScore.............." + value.commentScore)
        this.isCommented = true;
        this.commentScore = value.commentScore
        this.showScore();
      },
      orderData(value) {
        console.log(value)
        this.getOrder(value.order.oid, value.user)
      },
      orderCancelToAll(value) {
        this.isOrderComing = false;
      }
    },
    data:function() {
        return {
          isCommented:false,
          commentScore:Number,
          isOrderComing:false,
          liList:[{active:false},{active:false},{active:false},{active:false},{active:false}],
          order:{
            oid:'',
            getOn:'',
            getOff:'',
            onTime:''
          }
        }
    },
    methods:{
      showScore() {
        for(var i = 0; i < this.commentScore; i++) {
          this.liList[i].active = true;
        }
      },
      toIndex() {
          this.$router.push("/")
      },
      getOrder(oid, user) {
        this.$http.post(this.$store.state.baseUrl + "/getUserOrder", {oid:oid,uid:user.uid},{emulateJSON:true}).then(function(res){
          if(res.data) {
            console.log(res.data)
            this.order = res.data;
            this.order.uid =user.uid
            this.order.did = this.$store.state.driver.did
            this.order.username = user.username
            this.order.phoneNum = user.phoneNum
            this.order.onTime = res.data.onTime
            this.$store.state.driver.order = this.order
            this.isOrderComing = true;
          }
        })
      },
      takeOrder(){
        var This = this
        this.isOrderComing = false;
        console.log(this.order.oid)
        this.$socket.emit("groupId", this.order.oid)
        this.$socket.emit(this.order.oid  + "chatGroup")
        this.$socket.emit("takenSuccess", this.$store.state.driver)
        this.order = JSON.stringify(this.order)
        this.$http.post(this.$store.state.baseUrl+"/takeOrder", {order:this.order}, {emulateJSON:true}).then(function(res) {
          if(Object.prototype.toString.call(res.data) === "[object Object]") {
            console.log("true--------")
            This.$router.push({
              path:"/ordering",
              query:{
                oid:This.$store.state.driver.order.oid
              }
            })
          }else {
            this.flag = true
            this.error = res.data
            let timer = setTimeout(()=>{
              this.flag = false;
              clearTimeout(timer);
            },1000)
          }
        })
      },
      disappear() {
        this.isOrderComing = false;
      }
    },
    store
  }



