import "../../assets/js/base.js"
import Tou from '../Top/top'
import store from "../../assets/share/driverShare"
import OrderModel from '../orderModel/orderModel'
export default {
  name: "get-order",
  components:{
    Tou,
    OrderModel
  },
  data:function() {
    return {
      order: {
        username:'',
        getOn:'',
        getOff:'',
        onTime:'',
        pNum:'',
        luggages:'',
        phoneNum:'',
        oid:'',
        realCost:'',
        eitherCarried:false,
        driverName:''
      },
      comingOrder:{
        getOn:'',
        getOff:'',
        onTime:'',
      },
      isPhoneShow:false,
      userData:{
        cancel_username:'',
        cancel_getOn:'',
        cancel_onTime:'',
        cancel_getOff:''
      },
      cancelShow:false,
      isMessageComing:false,
      messageArr:[],
      isOrderComing:false
    }
  },
  sockets:{
    connect() {
      console.log("driver-ordering连接成功")
      this.$socket.emit("groupId", this.order.oid)
      this.$socket.emit(this.order.oid + "chatGroup")
    },
    orderCancel(value) {
      this.userData.cancel_username = value.userData.username;
      this.userData.cancel_getOn = value.userData.getOn;
      this.userData.cancel_getOff = value.userData.getOff;
      this.userData.cancel_onTime = value.userData.onTime;
      this.cancelShow = true;
    },
    userMessage(value) {
      this.isMessageComing = true;
      this.messageArr.push({"message":value, className:"userMes"})
    },
    orderData(value) {
      console.log(value)
      this.getOrder(value.order.oid, value.user)
    },
    orderCancelToAll(value) {
      this.isOrderComing = false;
    }
  },
  created:function() {
    if(this.$route.query.oid) {
      this.order.oid = this.$route.query.oid
    }
    console.log("order-oid............." + this.order.oid)
    this.getDriver()
    var This = this;
    this.$http.get(this.$store.state.baseUrl+"/getOrder", {params:{oid:this.order.oid}}).then(function(res) {
      if (res.data) {
        res.data.realCost = res.data.realCost.toFixed(2)
        This.order.username = res.data.username
        This.order.getOn = res.data.getOn
        This.order.getOff = res.data.getOff
        This.order.onTime = res.data.onTime
        This.order.pNum = res.data.pNum
        This.order.luggages = res.data.luggages
        This.order.phoneNum = res.data.phoneNum
        This.order.oid = res.data.oid
        This.order.realCost = res.data.realCost
        This.order.uid = res.data.uid
        This.order = res.data;
        This.showLine(res.data.onData, res.data.offData)
      }
    })
    console.log("innitORcer")
  },
  methods:{
    arrival() {
      this.$socket.emit("arrival", this.order.uid)
      this.$http.get(this.$store.state.baseUrl+"/arrival", {params:{oid:this.order.oid,uid:this.order.uid}}).then(function(res) {
        if(res.data) {
          this.$router.push({
            path:"/orderOver",
            query:{
              oid:this.order.oid,
            }
          })
          console.log(res.data)
        }
      })
    },
    showPhone() {
      this.isPhoneShow = !this.isPhoneShow;
    },
    disappear() {
      this.isPhoneShow = false;
    },
    cancelDisappear() {
      this.cancelShow = false;
    },
    showLine(onData, offData) {
      onData.onLat = parseFloat(onData.onLat).toFixed(6)
      onData.onLng = parseFloat(onData.onLng).toFixed(6)
      offData.offLat = parseFloat(offData.offLat).toFixed(6)
      offData.offLng = parseFloat(offData.offLng).toFixed(6)
      var map = new BMap.Map("container");
      map.centerAndZoom(new BMap.Point(onData.onLng, onData.onLat), 14);

      var driving = new BMap.DrivingRoute(map, {
        renderOptions: {
          map: map,
          autoViewport: true
        }
      });
      var start = new BMap.Point(onData.onLng, onData.onLat);
      var end = new BMap.Point(offData.offLng, offData.offLat);
      driving.search(start, end);
    },
    getDriver() {
      this.$http.get(this.$store.state.baseUrl+"/getDriver").then(function(res) {
        if(Object.prototype.toString.call(res.data) === "[object Object]") {
          this.$store.state.driver = res.data;
        }else {
          console.log("获取driver信息失败")
        }
      })
    },
    toIndex() {
      this.$router.push("/")
    },
    toChat() {
      this.$store.state.chatFresh = true;
      this.$router.push({
        name:"chat",
        params:{
          oid:this.order.oid,
          messageArr:this.messageArr
        }
      })
    },
    getOrder(oid, user) {
      this.$http.post(this.$store.state.baseUrl + "/getUserOrder", {oid:oid,uid:user.uid},{emulateJSON:true}).then(function(res){
        if(res.data) {
          console.log(res.data)
          this.comingOrder = res.data;
          this.comingOrder.uid =user.uid
          this.comingOrder.did = this.$store.state.driver.did
          this.comingOrder.username = user.username
          this.comingOrder.phoneNum = user.phoneNum
          this.comingOrder.onTime = res.data.onTime
          /*this.$store.state.driver.order = this.order*/
          this.isOrderComing = true;
        }
      })
    },
    takeOrder(){
      var This = this
      /*this.isOrderComing = false;*/

      this.$socket.emit("groupId", this.order.oid)
      this.$socket.emit(this.order.oid  + "chatGroup")
      this.$socket.emit("takenSuccess", this.$store.state.driver)
      this.comingOrder = JSON.stringify(this.comingOrder)
      this.$http.post(this.$store.state.baseUrl + "/takeOrder", {order:this.comingOrder}, {emulateJSON:true}).then(function(res) {
        if(Object.prototype.toString.call(res.data) === "[object Object]") {
          console.log("true--------")
          This.isOrderComing = false;
          /*This.$router.push({
            path:"/ordering",
            query:{
              oid:This.$store.state.driver.order.oid
            }
          })*/
        }else {
          this.flag = true
          this.error = res.data
          let timer = setTimeout(()=>{
            this.flag = false;
            clearTimeout(timer);
          },1000)
        }
      })
    },
    disappear() {
      this.isOrderComing = false;
    }
  },

  store
}



